/* Base Styles */
.landing-page {
  font-family: 'Myriad Pro', sans-serif;
  text-align: center;
  color: #333;
}

.navbar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  transition: all 0.5s ease;
  z-index: 1000;
  margin-top: 30px;
}

.navbar-content {
  display: flex;
  align-items: center;
  gap: 20px;
  transition: all 0.5s ease;
  flex-grow: 1; /* Ensures navbar content takes full space */
  justify-content: center; /* Keep nav links centered */
  position: absolute; /* Absolute positioning */
  left: 50%;
  transform: translateX(-50%); /* Center the navbar content */
}

.navbar.scrolled .navbar-content {
  gap: 10px; /* Reduce spacing between elements when scrolled */
  justify-content: center; /* Nav links remain centered */
}

/* Logo position */
.logo {
  flex: 1; /* Pushes logo to the left */
}

.logo img {
  height: 40px;
  width: auto;
  transition: all 0.5s ease;
  transform: translateX(-250px); /* Initially at far left */
}

.navbar.scrolled .logo img {
  transform: translateX(-90px); /* Moves closer to center when scrolled */
}

/* Contact button */
.contact-btn {
  background: #f2662a;
  border: none;
  padding: 10px 20px;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  transition: all 0.5s ease;
  margin-left: auto;
  white-space: nowrap; 
  transform: translateX(250px);

  
}
.read-more {
  background: #f2662a;
  border: none;
  padding: 10px 20px;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  transition: all 0.5s ease;
  margin-left: auto;
  white-space: nowrap; 
  margin-top: 30px;
}

.navbar.scrolled .contact-btn {
  transform: translateX(-70.52px);
  font-weight: 900;
  padding: 0%;
  height: 55.6px;
   /* Moves closer to center when scrolled */
}

/* Navigation links */
.navbar ul {
  display: flex;
  gap: 20px;
  transition: gap 0.3s ease;
  padding-left: 0;
  margin: 0; /* Reset margin to prevent unwanted spacing */
}

.navbar:not(.scrolled) ul {
  gap: 50px;
  display: flex;
  justify-content: center;
}

.navbar.scrolled ul {
  background-color: rgba(0, 0, 0, 0.8);
  border-top-left-radius: 15px; /* Top-left border-radius */
  border-bottom-left-radius: 15px; /* Bottom-left border-radius */
  transform: translateX(-60px);
  padding: 5px 15px;
  border-top-right-radius: 0px; /* Top-right border-radius */
  border-bottom-right-radius: 0px; /* Bottom-right border-radius */
 
}

.navbar ul li {
  list-style: none;
}

.navbar.scrolled ul li a {
  color: white; /* Change nav items color to white when scrolled */
}

.navbar.scrolled ul li a:hover {
  color: #f39c12; /* Hover color remains the same */
}

.navbar ul li a {
  color: black;
  text-decoration: none;
  padding: 10px;
  font-weight: bold;
  display: block;
  transition: color 0.3s ease;
  white-space: nowrap; 
}

.navbar ul li a:hover {
  color: #f39c12; /* Hover color */
}

nav ul li a::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  width: 8px;
  height: 8px;
  background: #f2662a;
  border-radius: 50%;
  transform: translateX(-50%) scale(0);
  transition: transform 0.2s ease-in-out;
}

nav ul li a:hover::after {
  transform: translateX(-50%) scale(1);
}



.hero-section {
  position: relative;
  margin: 0;
  padding: 0;
  overflow: visible;
}

.car-container {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
  background-image: url('../assets/background.png');
  background-size: 100% 100%;
  background-position: top left;
  background-repeat: no-repeat;
  margin-top: 40px;
  top: 50px;
}

.car {
  position: absolute;
  top: 0px;
  right: -100%;
  width: 700px;
  height: auto;
  transition: right 1.8s cubic-bezier(0.3, 1, 0.3, 1);
  filter: blur(10px);
}

.car img {
  width: 100%;
  height: auto;
}

.car.animate {
  right: calc(50% - 400px);
  top: 50px;
  filter: blur(0);
  animation: bounce 0.5s ease-out 1.5s;
}

@keyframes bounce {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(0);
  }
}

h1 {
  font-size: 32px;
  margin-top: 80px;
}

.highlight {
  color: #5d2d90;
}

.highlight-orange {
  color: #f2662a;
}

.smart-listening-section {
  padding: 6rem 3rem;
  background-color: #f9f9f9;
  text-align: center;
  position: relative;
}

.smart-listening-section h2 {
  font-size: 3rem;
  margin-bottom: 3rem;
  color: #333;
  font-weight: 700;
}

.listening-features {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
  position: relative;
  top: 100px;
}

.feature-card {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0px 8px 16px rgba(0, 0.1, 0, 0.2);
  padding: 2.5rem;
  width: 100%;
  max-width: 600px;
  text-align: left;
  position: relative;
  margin-bottom: 60px;
  transition: all 0.3s ease;
  opacity: 1;
  transform: scale(1);
  margin-top: 100px;
}

.feature-card.sticky {
  position: sticky;
  top: 0;
  z-index: 1;
  
}

.feature-card h3 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: #ff6f00;
  font-weight: 600;
  margin-top: 80px;
}

.feature-card p {
  font-size: 1.2rem;
  color: #555;
  line-height: 1.8;
}

.feature-benefits {
  list-style: none;
  padding: 0;
  margin-top: 1.5rem;
}

.feature-benefits li {
  font-size: 1rem;
  color: #444;
  margin-bottom: 0.8rem;
  display: flex;
  align-items: center;
}

.feature-benefits li::before {
  content: "✔";
  color: #28a745;
  margin-right: 0.5rem;
  font-size: 1.2rem;
}
.navbar.scrolled .logo img {
  background-color:#F7F7F7  ; /* Add background color */
  border-radius: 15px; /* Add border radius */
  padding: 5px 15px; /* Add padding to match the effect */
}

/* Contact button effect when scrolled */
.navbar.scrolled .contact-btn {
  background-color: rgba(0, 0, 0, 0.8); /* Add background color */
  border-top-left-radius: 0; /* Top-left border-radius */
  border-bottom-left-radius: 0; /* Bottom-left border-radius */
  border-top-right-radius: 15px; /* Top-right border-radius */
  border-bottom-right-radius: 15px; /* Bottom-right border-radius */ /* Add border radius */
  padding-right: 25px; /* Add padding to match the effect */
  height:55.6px;
}
/* Mobile Navbar */
.mobile-navbar {
  display: none; /* Initially hide the mobile navbar */
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: transparent;
 height: 0px;
  z-index: 1000;
  position: fixed;
  margin-left: 240px;

}

.mobile-navbar .logo {
  display: flex; /* Ensure the logo container is displayed */
  justify-content: center; /* Center the logo horizontally */
  align-items: center; /* Align vertically */
  width: 100%; /* Take full width for proper alignment */
  order: 1; /* Place the logo in the center */
}

.mobile-navbar .logo img {
  width: 120px; /* Adjust size for mobile */
  height: auto;
  margin: 0 auto;
  /* filter: drop-shadow(0 8px 20px rgba(0, 0, 0, 0.3)); */
}

.hamburger {
  font-size: 40px;
  cursor: pointer;
  border: none;
  background: transparent;
  outline: none;
  order: 2; /* Place the hamburger menu to the right */
}

.mobile-menu {
  position: absolute;
  top: 60px;
  right: 10px;
  background: white;
  width: 200px;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transform: translateY(-20px);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
  z-index: 1001; /* Ensure it's above other elements */
}

.mobile-menu.open {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

.mobile-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.mobile-menu ul li {
  padding: 12px 15px;
  border-bottom: 1px solid #ddd;
  text-align: center;
}

.mobile-menu ul li:last-child {
  border-bottom: none;
}

.mobile-menu ul li a {
  color: #333;
  text-decoration: none;
  font-size: 16px;
  display: block;
}

.mobile-menu ul li a:hover {
  color: #f2662a;
}
