  }
/* Responsive Styles */
@media (max-width: 768px) {
  .navbar {
    display: none; /* Hide desktop navbar on mobile */
  }

  .mobile-navbar {
    display: flex; /* Show mobile navbar on mobile */
    justify-content: center; /* Center align items */
  }

  .mobile-navbar .logo {
    order: 1; /* Place the logo in the center */
    margin: 0 auto; /* Center the logo horizontally */
  }

  .hamburger {
    order: 2; /* Place the hamburger menu to the right */
  }

  .hero-section h1 {
    font-size: 24px;
    margin-top: 40px;
  }

  .car-container {
    height: 300px;
  }

  .car {
    width: 100%;
  }

  .smart-listening-section {
    padding: 3rem 1rem;
  }

  .smart-listening-section h2 {
    font-size: 2rem;
    margin-bottom: 2rem;
  }

  .feature-card {
    padding: 1.5rem;
    max-width: 100%;
  }

  .feature-card h3 {
    font-size: 1.5rem;
  }

  .feature-card p {
    font-size: 1rem;
  }

  .feature-benefits li {
    font-size: 0.9rem;
  }

  .car {
    width: 100%; /* Make the car responsive */
  }

  .car.animate {
    right: 0; /* Center the car on mobile */
    left: 0;
    margin: 0 auto; /* Center horizontally */
  }

  .hero-section h1 {
    font-size: 24px;
    margin-top: 180px; /* Add top margin for mobile */
  }
  .distinct{
    margin-top:0px ;
    margin-left: 0px;
    margin-right: 0px;
    
    }
}
