*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  }
  body {
  font-family: 'Myriad Pro', sans-serif;
  line-height: 1.6;
  background-color: #f9f9f9;
  }
  .dashboard-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding: 20px;
  margin-right: 50px;
  }
 
  .sticky-image-section {
  position: sticky; 
  top: 50%; 
  transform: translateY(-50%); 
  align-self: flex-start; 
  z-index: 10; 
  width: 35%; 
  height: auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 20px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2), 0 6px 20px rgba(0, 0, 0, 0.19);
  border-radius: 15px;

  }
  .sticky-image-section:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3), 0 10px 30px rgba(0, 0, 0, 0.22);
  }
  .section-image {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  transition: opacity 0.5s ease-in-out;
  }
 
  .text-sections {
  width: 60%; 
  margin-left: 40%; 
  padding-left: 20px; 
  }
  .text-section {
  margin-bottom: 250px; 
  }
  .section-title {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
  }
  .section-description {
  font-size: 1.3rem;
  color: #555;
  margin-bottom: 15px;
  }


  /* Zoom Modal */
.zoom-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  cursor: zoom-out;
}

.zoom-image {
  max-width: 90%;
  max-height: 90%;
  border-radius: 8px;
}


  @media (max-width: 768px) {
    body {
      font-family: 'Myriad Pro', sans-serif;
      line-height: 1.6;
      background-color: #f9f9f9;
      }
      .dashboard-container {
      display: flex;
      flex-direction: column;
      min-height: 100vh;
      padding: 20px;
      margin-right: 50px;
      }
     
      .sticky-image-section {
      position: -webkit-sticky; 
      top: 50%; 
      transform: translateY(200%); 
      align-self: flex-start; 
      z-index: 10; 
      width: 55%; 
     
      height: auto;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-left: -10px; 
      }
      .section-image {
      max-width: 100%;
      height: auto;
      border-radius: 8px;
      transition: opacity 0.5s ease-in-out;
      }
     
      .text-sections {
      width: 60%; 
      margin-left: 40%; 
      padding-left: 20px; 
      }
      .text-section {
      margin-bottom: 50px; 
      margin-right: -40px;
      }
      .section-title {
      font-size: .7rem;
      margin-bottom: 20px;
      color: #333;
      }
      .section-description {
      font-size: 0.5rem;
      color: #555;
      margin-bottom: 15px;
      }
      


  }
 